@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  background-color: #2c2c2c;
  margin: 0;
  font-family: 'Lato', sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI',
    'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media (prefers-color-scheme: light) {
  body {
    background-color: #e4e5e4;
  }
}

#leaderboards {
  width: 80vw;
  max-width: 1000px;
  margin: auto;
}

table {
  border-collapse: collapse;
  width: 100%;
  margin: auto;
  margin-bottom: 20px;
}
th,
td {
  padding: 10px;
}

tr:nth-child(even) {
  background-color: #363535;
}

.user-best {
  border: 1px solid var(--border);
  color: #ffc1a7;
}

p.user-best {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
  padding: 10px;
  margin-bottom: 10px;
}

.leaderboard-entry .name,
.leaderboard-entry .pokemon {
  text-transform: capitalize;
}

@media (max-width: 780px) {
  #leaderboards {
    width: 95vw;
    font-size: 14px;
  }
}

@media (prefers-color-scheme: light) {
  .user-best {
    color: #b84a1c;
  }

  tr:nth-child(even) {
    background-color: #c5c5c5;
  }
}

:root {
  --black: #2c2c2c;
  --white: #e4e5e4;
  --border: #52504f;
}

.Pallet-Town-On-Parade {
  text-align: center;
  color: #e4e5e4;
  color: var(--white);
}

#title {
  margin-top: 10px;
  max-width: 500px;
  min-width: 300px;
  width: 60vw;
}

#gameboard {
  width: 100vw;
  max-width: 1347px;
  height: auto;
  margin-bottom: 20px;
}

.hidden-pokemon {
  padding: 5px 15px;
  width: 7vw;
  max-width: 70px;
  min-width: 40px;
  height: 7vw;
  max-height: 70px;
  min-height: 40px;
}

.remaining .hidden-pokemon {
  margin-bottom: 0;
}

dd {
  padding: 5px;
  margin-left: 0;
}

.start,
.yesBtn,
.noBtn,
.cancelBtn,
.addBtn {
  display: block;
  margin: auto;
  background-color: transparent;
  color: #e4e5e4;
  color: var(--white);
  padding: 8px 12px;
  font-size: 14px;
  border: none;
  cursor: pointer;
}

.yesBtn,
.noBtn,
.cancelBtn,
.addBtn {
  margin: 10px;
  display: inline-block;
}

.start,
.yesBtn,
.noBtn,
.addBtn,
.cancelBtn {
  border: 1px solid #e4e5e4;
  border: 1px solid var(--white);
  border-radius: 5px;
}

.start:hover {
  color: #ffc1a7;
  border: 1px solid #ffc1a7;
}

.yesBtn:hover,
.addBtn:hover {
  color: #59ee45;
  border: 1px solid #59ee45;
}

.noBtn:hover,
.cancelBtn:hover {
  color: #ff5656;
  border: 1px solid #ff5656;
}

.name-input {
  padding-bottom: 10px;
  display: block;
}

.name-input input {
  margin-left: 15px;
  background-color: transparent;
  color: #e4e5e4;
  color: var(--white);
  padding: 8px;
  font-size: 14px;
  border: 1px solid #e4e5e4;
  border: 1px solid var(--white);
  border-radius: 5px;
}

.pokeball {
  pointer-events: none;
  position: absolute;
  width: 5vw;
  min-width: 30px;
  max-width: 75px;
}

@media (max-width: 1920px) {
  #gameboard {
    max-width: 1200px;
  }
}

@media (max-width: 780px) {
  p,
  dd {
    max-width: 90vw;
    margin: auto;
  }
  p {
    -webkit-margin-before: 1em;
            margin-block-start: 1em;
    -webkit-margin-after: 1em;
            margin-block-end: 1em;
  }
}

@media (prefers-color-scheme: light) {
  .Pallet-Town-On-Parade {
    color: #2c2c2c;
    color: var(--black);
  }

  .start,
  .yesBtn,
  .noBtn,
  .cancelBtn,
  .addBtn,
  .name-input input {
    color: #2c2c2c;
    color: var(--black);
    border: 1px solid #2c2c2c;
    border: 1px solid var(--black);
  }

  .start:hover {
    color: #b84a1c;
    border: 1px solid #b84a1c;
  }

  .yesBtn:hover,
  .addBtn:hover {
    color: #12912d;
    border: 1px solid #12912d;
  }

  .noBtn:hover,
  .cancelBtn:hover {
    color: #da3333;
    border: 1px solid #da3333;
  }
}

